<template>
  <div>
    <div
      style="padding-top: 0.929rem;padding-bottom: 1.393rem;"
      class="dis-flex align-item-center justify-content-center"
    >
      <router-link to="/personal/personal-info">
        <van-image
          round
          width="4.429rem"
          height="4.429rem"
          style="margin-right: 0.929rem;"
          :src="perinfor.head_img"
        />
      </router-link>

      <div class="font-12 color-282828">
        <div v-if="type==1">{{perinfor.name}} （用户）</div>
        <div v-if="type==2">{{perinfor.name}} （牧民）</div>
        <div v-if="type==3">{{perinfor.name}} （屠宰）</div>
        <div style="margin-top: 1.464rem;">18300353634</div>
      </div>
    </div>

    <!--导航跳转-->
    <div v-for="(item, inx) of navList" :key="inx" class="nav-item-box">
      <van-cell :title="item.title" :to="item.route" is-link >
        <div slot="icon" class="tx-center" style="min-width: 1.857rem;margin-right: 0.821rem;">
          <img :src="item.icon" :style="item.iconStyle" />
        </div>
      </van-cell>
      <div
        v-if="item.isPartition === true"
        class="divider"
        style="height: 0.679rem;background-color: #F4F1F4;"
      ></div>
    </div>
  </div>
</template>

<script>
import {userInfo} from "@/api/api"
export default {
  name: "personal",
  data() {
    return {
      navList: [
        {
          title: "我的牦牛",
          route: "/personal/my-yak",
          icon: require("../../assets/images/personal/yak.png"),
          iconStyle: "width: 23px;height: 23px;"
        },
        {
          title: "我的订单",
          route: "/personal/my-order",
          icon: require("../../assets/images/personal/order.png"),
          iconStyle: "width: 20px;height: 24px;"
        },
        {
          title: "我的钱包",
          route: "/personal/my-wallet",
          icon: require("../../assets/images/personal/balance.png"),
          iconStyle: "width: 23.5px;height: 20px;"
        },
        {
          title: "我的卡劵",
          route: "/personal/my-card",
          icon: require("../../assets/images/personal/card-roll.png"),
          iconStyle: "width: 22px;height: 21px;",
          isPartition: true
        },
        {
          title: "我要送礼",
          route: "/personal/giftgiving",
          icon: require("../../assets/images/personal/gift.png"),
          iconStyle: "width: 24px;height: 24px;"
        },
        {
          title: "我要销售",
          route: "/personal/my-sales",
          icon: require("../../assets/images/personal/sale.png"),
          iconStyle: "width: 24px;height: 18.5;",
          isPartition: true
        },
        {
          title: "实名认证",
          route: "/personal/authentication",
          icon: require("../../assets/images/personal/auth.png"),
          iconStyle: "width: 23px;height: 18.5;"
        },
        {
          title: "我的地址",
          route: "/personal/my-address",
          icon: require("../../assets/images/personal/address.png"),
          iconStyle: "width: 17.5px;height: 22.5px;"
        },
        {
          title: "我的菜品",
          route: "/personal/my-dishes",
          icon: require("../../assets/images/personal/food.png"),
          iconStyle: "width: 23.5px;height: 23.5px;",
          isPartition: true
        },
        {
          title: "我的预约",
          route: "/personal/my-reservation",
          icon: require("../../assets/images/personal/subscribe.png"),
          iconStyle: "width: 22px;height: 22px;"
        },
        {
          title: "我的帮扶",
          route: "/personal/my-support",
          icon: require("../../assets/images/personal/loving-help.png"),
          iconStyle: "width: 25.5px;height: 21.5px;"
        },
        {
          title: "设置",
          route: "/personal/my-setup",
          icon: require("../../assets/images/personal/setting.png"),
          iconStyle: "width: 23.5px;height: 22.5px;"
        }
      ],
      perinfor:{},
      type:0
    };
  },
  created() {
    this.type=localStorage.getItem("type")
    let data = {
      move_id: localStorage.getItem("id"),
      type: localStorage.getItem("type")
    }
    userInfo(data).then(res=>{
      this.perinfor = res.data.data
    })
  },
};
</script>

<style scoped lang="less">
/*导航栏 Item 容器*/
.nav-item-box {
  /deep/ .van-cell {
    padding: 0.714rem 1.143rem 0.714rem 1.643rem;
  }

  /deep/ .van-cell__title {
    padding-left: 0.429rem;
    font-size: 0.929rem;
    color: #989898;
    border-bottom: 1px solid #f2f2f2;
  }

  /deep/.van-cell__right-icon {
    color: #989898;
  }
}
</style>
